.AuthStatus {
    display: flex;
}

.AuthStatus .sign-out, .sign-in {
    color: var(--white-ish);
}

.AuthStatus .sign-out button, .sign-in button {
    border: none;
    outline: none;
    background: transparent;
    color: var(--white-ish);
    font-size: 15px;
    cursor: pointer;
    padding: 0 .5rem;
    transition: filter .3s;
}

.AuthStatus .sign-out button:hover, .sign-in button:hover {
    filter: brightness(1.5);
}

