:root {
    --dark-grey: #485877;
    --light-grey: #beddf387; 
    --white-ish: rgb(223, 223, 223);
	--enabled-color: #ffb446;
	--error-color: #ff9800;
	--border-radius: 4px;
}

body {
    background-color: var(--light-grey);
}

.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}